@charset "UTF-8";
.company .fk-mainContent {
  background: url("/assets/images/object/bg_obje01.svg") no-repeat right 3.5%/62%, url("/assets/images/object/bg_obje01.svg") no-repeat left 37%/62%, url("/assets/images/object/bg_obje01.svg") no-repeat right 74%/62%;
}

.fk-example {
  margin-top: 9.9vw;
  padding-bottom: 5vw;
}
@media screen and (min-width: 1920px) {
  .fk-example {
    margin-top: 11.88rem;
    padding-bottom: 6rem;
  }
}
@media screen and (max-width: 767px) {
  .fk-example {
    margin-top: 22.39vw;
    padding-bottom: 28.5vw;
  }
}
.fk-example .fk-headingWrap {
  margin-bottom: 5vw;
}
@media screen and (min-width: 1920px) {
  .fk-example .fk-headingWrap {
    margin-bottom: 6rem;
  }
}
@media screen and (max-width: 767px) {
  .fk-example .fk-headingWrap {
    margin-bottom: 10.18vw;
  }
}
.fk-example__block {
  margin: 0 6.25vw;
}
@media screen and (min-width: 1920px) {
  .fk-example__block {
    margin: 0 7.5rem;
  }
}
.fk-example__list {
  margin-bottom: 10.42vw;
  display: flex;
  column-gap: 3.13vw;
  row-gap: 8.33vw;
  flex-wrap: wrap;
}
@media screen and (min-width: 1920px) {
  .fk-example__list {
    margin-bottom: 12.5rem;
    column-gap: 3.75rem;
    row-gap: 10rem;
  }
}
@media screen and (max-width: 767px) {
  .fk-example__list {
    margin-bottom: 8.14vw;
    column-gap: 4.07vw;
    row-gap: 6.11vw;
  }
}
.fk-example__listItem {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  flex-basis: calc((100% - 6.25vw) * 0.3333);
  height: 16.67vw;
  display: flex;
  align-items: center;
  overflow: hidden;
}
@media screen and (min-width: 1920px) {
  .fk-example__listItem {
    flex-basis: calc((100% - 7.5rem) * 0.3333);
    height: 320px;
  }
}
@media screen and (max-width: 767px) {
  .fk-example__listItem {
    flex-basis: calc((100% - 8.14vw) * 0.3333);
    height: 16.54vw;
  }
}
.fk-example__list :hover {
  transform: scale(1.2);
  transition: all 0.5s;
}

.img-hide {
  display: none;
}

button.more {
  font-family: "Noto Serif JP", "Noto Sans JP", sans-serif;
  font-weight: bold;
  text-align: center;
  width: 200px;
  margin: 20px auto;
  display: block;
  background-color: #FADA89;
  padding: 10px 15px;
  border: none;
  outline: 0;
  transition: 0.5s;
  -erbkit-transition: 0.5s;
}

button.more::after {
  content: "もっと見る";
  transition: 0.2s;
  -erbkit-transition: 0.2s;
}

button.more.on-click::after {
  content: "閉じる";
}